import { createSlice, PayloadAction } from "@reduxjs/toolkit";

interface TodoItem {
  taskID: number
  taskText: string
  taskDate: any
  check: boolean
}

interface MainState {
  currentNewTask: string
  tasks: TodoItem[]
  counterID: number
}

const initialState: MainState = {
  currentNewTask: "",
  tasks: [],
  counterID: 0,
}

const mainSlice = createSlice({
  name: "main",
  initialState: initialState,
  reducers: {
    INITIALIZE (state) {
      let count = 0;
      let maxId = 0;

      let tempArr: TodoItem[]  = [];
      for (let i = 0; i < localStorage.length; i++) {
        if (Number(localStorage.key(i))) {
       
          let index: string | null = localStorage.key(i)

          if (index) {
            let item = JSON.parse(localStorage.getItem(index.toString()) || '')
            tempArr.push(item)
            count++
          }           
        }
      }

      state.tasks = tempArr.sort((a, b) => a.taskID - b.taskID)

      state.tasks.forEach( item => {
        if (item.taskID > maxId) {
          maxId = item.taskID
        }
      })

      state.counterID = maxId
    },

    SET_TASK(state, action: PayloadAction<{currentTask: string}>) {
      state.currentNewTask = action.payload.currentTask;
    },

    ADD_TASK(state, action: PayloadAction<{taskDate: any}>) {
      state.counterID = state.counterID + 1;

      let item = {
        taskID: state.counterID,
        taskText: state.currentNewTask,
        taskDate: action.payload.taskDate,
        check: false,
      }

      state.tasks.push(item);
      localStorage.setItem(`${state.counterID}`, JSON.stringify(item))
      state.currentNewTask = "";
    },

    DELETE_TASK(state, action: PayloadAction<{id: number}>) {
      state.tasks = state.tasks.filter(
        (task) => task.taskID !== action.payload.id
      );
      localStorage.removeItem(`${action.payload.id}`)
    },

    CHECK_TASK(state, action: PayloadAction<{id: number}>) {
      state.tasks = state.tasks.map(task => {
        if (task.taskID === action.payload.id) {
          if (task.check) {

            localStorage.setItem(`${action.payload.id}`, JSON.stringify({ ...task, check: false }))

            return { ...task, check: false }

          } else {

            localStorage.setItem(`${action.payload.id}`, JSON.stringify({ ...task, check: true }))

            return { ...task, check: true }
          
          }
        }
        return task;
      });
    },
  }
});

export const { SET_TASK, ADD_TASK, DELETE_TASK, CHECK_TASK, INITIALIZE } = mainSlice.actions;

export default mainSlice.reducer;