import { createSlice, PayloadAction } from "@reduxjs/toolkit/";

interface SettingsInitialState {
    currentBG: number
    currentLanguage: string
    darkMode: boolean
}

const initialState: SettingsInitialState = {
    currentBG: 1,
    currentLanguage: 'en',
    darkMode: false,
}

const settingsSlice = createSlice({
    name: 'settings',
    initialState: initialState,
    reducers: {
        INITIALIZE_SETTINGS(state) {

            if (localStorage.getItem('currentBG')) {
                state.currentBG = +localStorage.getItem('currentBG')!;
            }

            if (localStorage.getItem('currentLanguage')) {
                state.currentLanguage = localStorage.getItem('currentLanguage')!;
            }

            if (localStorage.getItem('darkMode')) {
                if (localStorage.getItem('darkMode') === 'false') {
                    state.darkMode = false
                } else if (localStorage.getItem('darkMode') === 'true') {
                    state.darkMode = true
                }
            }
            
        },

        CHANGE_BG(state, action: PayloadAction<{bgID: number}>) {
            state.currentBG = action.payload.bgID;
            localStorage.setItem('currentBG', `${action.payload.bgID}`)
        },

        CHANGE_LANGUAGE(state, action: PayloadAction<{language: string}>) {
            state.currentLanguage = action.payload.language;
            localStorage.setItem('currentLanguage', `${action.payload.language}`)
        },

        CHANGE_MODE(state, action: PayloadAction<boolean>) {
            state.darkMode = action.payload;
            localStorage.setItem('darkMode', `${action.payload}`)
        }
    }
})

export const { CHANGE_BG, CHANGE_LANGUAGE, CHANGE_MODE, INITIALIZE_SETTINGS } = settingsSlice.actions;

export default settingsSlice.reducer;