import "./Menu.scss";
import { NavLink } from "react-router-dom";
import React, { useState } from "react";
import HomeIcon from "@mui/icons-material/Home";
import BarChartIcon from "@mui/icons-material/BarChart";
import SettingsIcon from "@mui/icons-material/Settings";

interface MenuProps {
  currentLanguage: string;
  darkMode: boolean;
}

const Menu: React.FC<MenuProps> = ({ currentLanguage, darkMode }) => {
  const [isMenuOpen, toggleMenu] = useState(false);

  const menuHandler = () => {
    const screenWidth = document.documentElement.scrollWidth;
    screenWidth < 767 ? toggleMenu(false) : toggleMenu(true);
  }

  return (
    <nav className={!darkMode 
    ? !isMenuOpen ? "menu" : "menu menu-open"
    : !isMenuOpen ? "menu menu-dark" : "menu menu-open menu-dark"}>
      <ul className="menu__list" 
      onMouseOver={menuHandler} 
      onMouseOut={() => toggleMenu(false)}>
        <NavLink
          className={({ isActive }) =>
            !darkMode
              ? isActive
                ? "menu__link menu__link-active"
                : "menu__link"
              : isActive
              ? "menu__link menu__link-active"
              : "menu__link menu__link-dark"
          }
          to={"/home"}
        >
          {!isMenuOpen ? (
            <HomeIcon />
          ) : (
            <span>
                <HomeIcon />
              {currentLanguage === "en" ? "Home" : "Главная"}
              
            </span>
          )}
        </NavLink>

        <NavLink
          className={({ isActive }) =>
            !darkMode
              ? isActive
                ? "menu__link menu__link-active"
                : "menu__link"
              : isActive
              ? "menu__link menu__link-active"
              : "menu__link menu__link-dark"
          }
          to={"/stats"}
        >
          {!isMenuOpen ? (
            <BarChartIcon />
          ) : (
            <span>
                <BarChartIcon />
              {currentLanguage === "en" ? "Stats" : "Статистика"}
              
            </span>
          )}
        </NavLink>
        <NavLink
          className={({ isActive }) =>
            !darkMode
              ? isActive
                ? "menu__link menu__link-active"
                : "menu__link"
              : isActive
              ? "menu__link menu__link-active"
              : "menu__link menu__link-dark"
          }
          to={"/settings"}
        >
          {!isMenuOpen ? (
            <SettingsIcon />
          ) : (
            <span>
                <SettingsIcon />
              {currentLanguage === "en" ? "Settings" : "Настройки"}
              
            </span>
          )}
        </NavLink>
      </ul>
    </nav>
  );
};

export default Menu;
