import "./EmptyList.scss";
import React from "react";

interface EmptyListProps {
    tasksCount: number;
    currentLanguage: string;
}

const EmptyList: React.FC<EmptyListProps> = ({ tasksCount, currentLanguage }) => {
    return (
        <div className="emptyList">
            {!tasksCount
                ? currentLanguage === "en"
                    ? "Nothing yet..."
                    : "Пока ничего..."
                : null}
        </div>
    );
};

export default EmptyList;
